import { getButtonVariants } from './button';
import { AlertDialogPrimitive } from '@vanilla-primitives/core';
export class AlertDialogCancel extends AlertDialogPrimitive.Cancel {
	connectedCallback() {
		this.className = `${getButtonVariants({ variant: 'outline' })} mt-2 sm:mt-0`;
		super.connectedCallback();
	}
}
// These are just basically a div elements so you can just use a div element if you want this is just to feature match with Shadcn
export class AlertDialogHeader extends HTMLElement {
	connectedCallback() {
		this.className = 'flex flex-col space-y-2 text-center sm:text-left';
	}
}
export class AlertDialogFooter extends HTMLElement {
	connectedCallback() {
		this.className = 'flex flex-col-reverse sm:flex-row sm:justify-end sm:space-x-2 mt-4';
	}
}

export class AlertDialogTitle extends AlertDialogPrimitive.Title {
	connectedCallback() {
		this.className = 'text-lg font-semibold';
		super.connectedCallback();
	}
}
export class AlertDialogDescription extends AlertDialogPrimitive.Description {
	connectedCallback() {
		this.className = 'text-sm text-muted-foreground';
		super.connectedCallback();
	}
}

export class AlertDialogContent extends AlertDialogPrimitive.Content {
	connectedCallback() {
		this.className = `z-50 w-full max-w-lg gap-4 border bg-background p-6 shadow-lg duration-200 sm:rounded-lg
        data-[state=open]:animate-in 
        data-[state=closed]:animate-out 
        data-[state=closed]:fade-out-0 
        data-[state=open]:fade-in-0 
        data-[state=closed]:zoom-out-95 
        data-[state=open]:zoom-in-95
        backdrop:bg-black/80
        backdrop:data-[state=open]:animate-in
        backdrop:data-[state=closed]:animate-out
        backdrop:data-[state=closed]:fade-out-0
        backdrop:data-[state=open]:fade-in-0`;
		super.connectedCallback();
	}
}
export const AlertDialogTrigger = AlertDialogPrimitive.Trigger;
export const AlertDialog = AlertDialogPrimitive.Root